import * as actionType from './cartActionType';

export const addRecipe = recipe => ({
  type: actionType.ADD_RECIPE,
  payload: recipe
});

export const removeRecipe = recipe => ({
  type: actionType.REMOVE_RECIPE,
  payload: recipe
});

export const increment = recipe => ({
  type: actionType.INCREMENT_QUANTITY,
  payload: recipe
});

export const decrement = recipe => ({
  type: actionType.DECREMENT_QUANTITY,
  payload: recipe
});