import React, { Component } from 'react';
import { IconContext } from 'react-icons';
import { FaRegWindowClose } from 'react-icons/fa';

import './Signin.scss';
import Input from '../Input/Input';
import AuthButton from '../AuthButton/AuthButton';
import Backdrop from '../Backdrop/Backdrop';
import TextError from '../TextError/TextError';
import Popup from '../Popup/Popup';
import { auth } from '../../firebase/firebase';

class Signin extends Component {
  constructor(props) {
    super(props);
    this.inputRef = React.createRef();
    this.state = {
      email: '',
      password: '',
      emailError: null,
      error: null,
      loading: false
    }
  }

  componentDidMount() {
    this.inputRef.current.focus();
  }

  handleChange = evt => {
    const { name, value } = evt.target;
    this.setState({ [name]: value });
  }

  handleSubmit = async evt => {
    evt.preventDefault();
    const { email, password } = this.state;

    if (email.trim() === '') {
      this.setState({ emailError: 'enter your email' });
      return;
    }

    if (!password.trim()) {
      this.setState({ emailError: '', error: 'enter password' });
      return;
    }

    if (email.trim() !== '' && password.trim() !== '') {
      try {
        this.setState({ loading: true });
        await auth.signInWithEmailAndPassword(email, password);
        this.props.close();
        this.setState({ loading: false });
      } catch(error) {
        this.setState({ error: 'something went wrong', emailError: null, loading: false });
      }
    }
  }

  render() {
    const { show, close, showSignup } = this.props;
    const { emailError, error, loading } = this.state;

    return(
      <div className='signinContainer'>
        {show && <Backdrop />}
        <Popup show={show}>
          <div className='title'>
            <h3>Signup to Pizza hut</h3>
            <IconContext.Provider value={{className: 'iconClose'}}>
              <FaRegWindowClose onClick={close}/>
            </IconContext.Provider>
          </div>
          <div className='form'>
            <form onSubmit={this.handleSubmit}>
              <Input id='email' name='email' label='Email' type='email' ref={this.inputRef} change={this.handleChange} />
              {emailError && <TextError>{emailError}</TextError>}
              <Input id='password' name='password' label='Password' type='password' change={this.handleChange}/>
              {error && <TextError>{error}</TextError>}
              {loading ? <AuthButton disabled>loggin in</AuthButton> : <AuthButton type='submit'>login</AuthButton>}
            </form>
          </div>
          <p className='link'>New to Pizza hut? <span onClick={showSignup} className='create'>Create account</span></p>
        </Popup>
      </div>
    );
  }
}

export default Signin;