const syncLocalStorage = list => {
  window.localStorage.setItem('likes', JSON.stringify(list));
};

export const likeItem = (currentItems, itemToAdd) => {
  const hasItem = currentItems.find(item => item.recipe_id === itemToAdd.recipe_id);
  
  let likes = [];
  if (!hasItem) {
    likes = currentItems.concat(itemToAdd);
    syncLocalStorage(likes);
    return likes;
  } 

  likes = currentItems;
  syncLocalStorage(likes);
  return likes;
};

export const unlikeItem = (currentItems, itemToRemove) => {
  const hasItem = currentItems.find(item => item.recipe_id === itemToRemove.recipe_id);
  
  let likes = [];
  if (!hasItem) {
    likes = currentItems;
    syncLocalStorage(likes);
    return likes;
  }

  likes = currentItems.filter(item => item.recipe_id !== itemToRemove.recipe_id);
  syncLocalStorage(likes);
  return likes;
};

export const isLiked = (favourites, recipe) => {
  return favourites.findIndex(item => item.recipe_id === recipe.recipe_id) !== -1;
};