import * as actionType from './favouriteActionType';
import { likeItem, unlikeItem } from '../../utility/favourites';

const checkRecipeStored = () => {
  const recipes = JSON.parse(window.localStorage.getItem('likes'));
  return recipes;
};

const initialState = {
  list: checkRecipeStored() || []
};

const favouriteReducer = (state = initialState, action) => {
  switch(action.type) {
    case actionType.LIKE_RECIPE:
      return {
        ...state,
        list: likeItem(state.list, action.payload)
      }
    case actionType.UNLIKE_RECIPE:
      return {
        ...state,
        list: unlikeItem(state.list, action.payload)
      }
    default: {
      return state;
    }
  }
};

export default favouriteReducer;