import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';

import './Favourites.scss';
import CartItem from '../../components/CartItem/CartItem';
import { selectList } from  '../../Redux/favourites/favouriteSelector';
import { unlikeRecipe } from '../../Redux/favourites/favouriteAction';

class Favourites extends Component {
  render() {
    const { likes, unlike } = this.props;

    return(
      <div className='favourites'>
        <div className='titleContainer'>
          <h3>Favourites</h3>
          <p>recipe added to your favourites</p>
        </div>
        <div>
          {
            likes.length > 0 ? likes.map(recipe => (
              <CartItem key={recipe.recipe_id} recipe={recipe} click={() => unlike(recipe)}/>
            )) : <p className='emptyMsg'>favourites empty</p>
          }
        </div>
      </div>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  likes: selectList
});

const mapDispatchToProps = dispatch => ({
  unlike: recipe => dispatch( unlikeRecipe(recipe) )
});

export default connect(mapStateToProps, mapDispatchToProps)(Favourites);