import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import { IconContext } from 'react-icons';
import { FaTrash, FaPlus, FaMinus } from 'react-icons/fa';

import './CartItem.scss';
import Popup from '../Popup/Popup';
import Confirmation from '../Confirmation/Confirmation';
import Backdrop from '../Backdrop/Backdrop';
import { recipePrice } from '../../utility/recipe';

const CartItem = ({ recipe, history, click, wishlist, increase, decrease }) => {
  const [ showPopup, setShowPopup ] = useState(false);

  const togglePopup = () => {
    setShowPopup(!showPopup);
  };

  const viewRecipeDetails = () => {
    if (recipe.title.toLowerCase().includes('pizza' || 'pizzas')) {
      history.push(`/recipe/pizza/${recipe.recipe_id}`);
      return;
    } else {
      history.push(`/recipe/pasta/${recipe.recipe_id}`);
      return;
    }
  };

  return(
    <div>
      {showPopup && <Backdrop close={togglePopup}/>}
      {showPopup && <Popup show={togglePopup}><Confirmation title='Confirm' content='Are you sure, you want to delete this recipe?' yes={click} no={togglePopup}/></Popup>}
      <div className='cartItem'>
        <div className='recipe'>
          <div className='imageContainer' onClick={viewRecipeDetails} title='view full details'>
            <img src={recipe.image_url} alt='recipe'/>
          </div>
          <div className='details'>
            <h5>{recipe.title}</h5>
            <p className='publisher'>Publisher: {recipe.publisher}</p>
            <span className='price'>Price: $ {recipePrice(recipe.recipe_id)}</span>
            {
              wishlist && <IconContext.Provider value={{className: 'iconAddRemove'}}>
                <span className='quantity'>Quantity: <FaMinus onClick={decrease} /> {recipe.quantity} <FaPlus onClick={increase}/></span>
              </IconContext.Provider>
            }
          </div>
        </div>
        <div className='remove'>
          <IconContext.Provider value={{className: 'iconRemove'}}>
            <FaTrash title='remove recipe from list' onClick={togglePopup}/>
          </IconContext.Provider>
        </div>
      </div>
    </div>
  );
};

export default withRouter(CartItem);