import React, { Component } from 'react';
import axios from 'axios';

import './RecipeInfo.scss';
import RecipeDetails from '../../components/RecipeDetails/RecipeDetails';

class RecipeInfo extends Component {
  state = {
    recipeInfo: null
  }

  async componentDidMount() {
    const id = this.props.match.params.recipeId;
    const info = await axios.get(`https://forkify-api.herokuapp.com/api/get?rId=${id}`);
    this.setState({ recipeInfo: info.data.recipe });
  }

  render() {
    const { recipeInfo } = this.state;

    return(
      <div className='recipeInfo'>
        <RecipeDetails info={recipeInfo}/>
      </div>
    );
  }
}

export default RecipeInfo;