import React from 'react';
import { withRouter, NavLink } from 'react-router-dom';
import './Recipe.scss';

import { recipePrice, rating } from '../../utility/recipe';

const Recipe = ({ match, recipe }) => {
  const currentUrl = match.url;

  const limitTitle = (title, limit = 17) => {
    if (title.length > limit) {
      const crtTitle = [];

      title.split(' ').reduce((acc, cur) => {
        if (acc + cur.length < limit) {
          crtTitle.push(cur);
        }
        return acc + cur.length;
      }, 0);

      return `${crtTitle.join(' ')}...`;
    }

    return title;
  };

  return(
    <div className='recipeContainer'>
      <div className='recipe'>
        <div className='imageContainer' style={{backgroundImage: `url(${recipe.image_url})`}}/>
        <div className='recipeDetail'>
          <h5>{limitTitle(recipe.title)}</h5>
          <span className='publisher'>{recipe.publisher}</span>
          <span className='rating'>{rating()}</span>
          <span className='price'>$ {recipePrice(recipe.recipe_id)}</span>
          <NavLink to={`${currentUrl}/${recipe.recipe_id}`} className='view' title='view full details of this recipe'>details</NavLink>
        </div>
      </div>
    </div>
  );
};

export default withRouter(Recipe);