export const recipePrice = id => {
  let price = 0;
  if (id) {
    price = parseInt(id.slice(0, 2));
  }
  if (price < 30) {
    price = 25;
  }
  if (isNaN(price)) {
    price = 30;
  }
  
  return price;
};

export const rating = () => {
  const rate = ['3.5', '3.8', '4.3', '4.5', '4.7', '4.8', '5.0'];

  return rate[Math.floor(Math.random() * rate.length)];
};