import React from 'react';
import { withRouter } from 'react-router-dom';

import './HowToCook.scss';
import Button from '../Button/Button';

const HowToCook = ({ sourceUrl, recipeName }) => {
  return(
    <div className='howToCook'>
      <div className='titleContainer'>
        <h3>how to cook</h3>
        <p>make recipe from home</p>
      </div>
      <div className='direction'>
        <p className='hint'>Checkout the link below. Here is the direction, which help you to prepare <span className='recipeName'>{recipeName}</span> in home</p>
        <a href={`${sourceUrl}`} rel="noopener noreferrer" target='_blank'>
          <Button primary>direction</Button>
        </a>
      </div>
    </div>
  );
};

export default withRouter(HowToCook);