import React, { useState } from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { IconContext } from 'react-icons';
import { FaRegHeart, FaHeart } from 'react-icons/fa';

import './RecipeDetails.scss';

import Button from '../Button/Button';
import IconButton from '../IconButton/IconButton';
import Spinner from '../Spinner/Spinner';
import Ingredients from '../Ingredients/Ingredients';
import HowToCook from '../HowToCook/HowToCook';
import Snackbar from '../Snackbar/Snackbar';

import { selectRecipes } from '../../Redux/cart/cartSelector';
import { selectList } from '../../Redux/favourites/favouriteSelector';
import { recipePrice } from '../../utility/recipe';
import { addRecipe, removeRecipe } from '../../Redux/cart/cartAction';
import { unlikeRecipe, likeRecipe } from '../../Redux/favourites/favouriteAction';
import { hasRecipe } from '../../utility/cart';
import { isLiked } from '../../utility/favourites';

const RecipeDetails = ({ info, cartItems, favourites, addRecipe, removeRecipe, like, unlike }) => {
  const [ showSnackbar, setShowSnackbar ] = useState(false);
  const [ type, setType ] = useState('');

  const addToCart = info => {
    setShowSnackbar(true);
    addRecipe(info);
    setType('added to');

    setTimeout(() => {
      setShowSnackbar(false);
    }, 2000)
  };

  const removeFromCart = info => {
    setShowSnackbar(true);
    removeRecipe(info);
    setType('removed from');

    setTimeout(() => {
      setShowSnackbar(false);
    }, 2000)
  };

  return(
    <div>
      {showSnackbar && <Snackbar text={`recipe ${type} wishlist`}/>}
      {info ? <div className='recipeDetails'>
        <div className='infoTitle'>
          <h3>{info.title}</h3>
          <p>Publisher: {info.publisher}</p>
        </div>
        <div className='imageBg' style={{backgroundImage: `url(${info.image_url})`}} />
        <div className='btnContainer'>
          <div className='price'>
            <p>Cost: $ {recipePrice(info.recipe_id)}</p>
          </div>
          <div className='buttons'>
            <IconContext.Provider value={{className: 'iconHeart'}}>
              <span>
                {isLiked(favourites, info) ? 
                <IconButton title='remove from favourite' click={() => unlike(info)}><FaHeart /></IconButton>
                : <IconButton title='add to favourite' click={() => like(info)}><FaRegHeart color='#1C1C1C'/></IconButton>}
              </span>
            </IconContext.Provider>
            <span>
              {hasRecipe(cartItems, info) ? 
              <Button title='remove from wishlist' primary click={() => removeFromCart(info)}>remove from cart</Button>
              : <Button title='add to wishlist' primary click={() => addToCart(info)}>add to cart</Button>}
            </span>
          </div>
        </div>
        <Ingredients ingredients={info.ingredients}/>
        <HowToCook sourceUrl={info.source_url} recipeName={info.title}/>
      </div> : <Spinner />}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  cartItems: selectRecipes,
  favourites: selectList
});

const mapDispatchToProps = dispatch => ({
  addRecipe: recipe => dispatch( addRecipe(recipe) ),
  removeRecipe: recipe => dispatch( removeRecipe(recipe) ),
  like: recipe => dispatch( likeRecipe(recipe) ),
  unlike: recipe => dispatch( unlikeRecipe(recipe) )
});

export default connect(mapStateToProps, mapDispatchToProps)(RecipeDetails);