import * as actionType from './cartActionType';
import { additem, removeItem, incrementQuantity, decrementQuantity  } from '../../utility/cart';

const checkRecipeStored = () => {
  const recipes = JSON.parse(window.localStorage.getItem('recipes'));
  return recipes;
};

const initialState = {
  recipes: checkRecipeStored() || []
};

const cartReducer = (state = initialState, action) => {
  switch(action.type) {
    case actionType.ADD_RECIPE:
      return {
        ...state,
        recipes: additem(state.recipes, action.payload)
      }
    case actionType.REMOVE_RECIPE:
      return {
        ...state,
        recipes: removeItem(state.recipes, action.payload)
      }
    case actionType.INCREMENT_QUANTITY:
      return {
        ...state,
        recipes: incrementQuantity(state.recipes, action.payload)
      }
    case actionType.DECREMENT_QUANTITY:
      return {
        ...state,
        recipes: decrementQuantity(state.recipes, action.payload)
      }
    default:
      return state;
  }
};

export default cartReducer;