import React, { Component } from 'react';
import { IconContext} from 'react-icons';
import { FaRegWindowClose } from 'react-icons/fa';

import './Signup.scss';
import Input from '../Input/Input';
import Backdrop from '../Backdrop/Backdrop';
import AuthButton from '../AuthButton/AuthButton';
import TextError from '../TextError/TextError';

import { auth } from '../../firebase/firebase';

class Signup extends Component {
  constructor(props) {
    super(props);
    this.inputRef = React.createRef();
    this.state = {
      email: '',
      password: '',
      confirmPassword: '',
      error: '',
      loading: false
    }
  }

  componentDidMount() {
    this.inputRef.current.focus();
  }

  handleChange = evt => {
    const { name, value } = evt.target;
    this.setState({ [name]: value });
  }

  handleSubmit = async evt => {
    evt.preventDefault();
    const { email, password, confirmPassword } = this.state;

    if (email === '') {
      this.setState({ error: 'enter your email' });
      return;
    }

    if (password === '') {
      this.setState({ error: 'create a password' });
      return;
    }

    if (confirmPassword === '') {
      this.setState({ error: 're-enter password' });
      return;
    }

    if (password !== confirmPassword) {
      this.setState({ error: 'password not match' });
      return;
    }

    if (email.trim() !== '' && password.trim() !== '' && confirmPassword.trim() !== '') {
      try {
        this.setState({ loading: true });
        await auth.createUserWithEmailAndPassword(email, password);
        this.props.close();
        this.setState({ loading: false });
      } catch(error) {
        this.setState({ signinError: true, loading: false });
      }
    }
  }

  render() {
    const { show, close, showSignin } = this.props;
    const { loading, error } = this.state;
  
    return(
      <div>
        {show && <Backdrop close={close}/>}
        <div className='signup'>
          <div className='titleContainer'>
            <div>
              <h3>Signup to Pizza hut</h3>
              <p>create an account to place order</p>
            </div>
            <IconContext.Provider value={{className: 'iconClose'}}>
              <FaRegWindowClose onClick={close}/>
            </IconContext.Provider>
          </div>
          <div className='signupContainer'>
            <form onSubmit={this.handleSubmit}>
              <Input name='email' id='email' type='email' label='Email' change={this.handleChange} ref={this.inputRef}/>
              <Input name='password' id='password' type='password' label='Create Password' change={this.handleChange}/>
              <Input name='confirmPassword' id='confirmPassword' type='password' label='Confirm Password' change={this.handleChange}/>
              {error && <TextError>{error}</TextError>}
              {loading ? <AuthButton disabled>creating account</AuthButton>: <AuthButton type='submit'>create account</AuthButton>}
              <p className='termsCondition'>By signing in you accept our <span className='terms'>Terms and Conditions</span></p>
            </form>
          </div>
          <p style={{textAlign: 'center', color: 'grey'}}>or</p>
          <p style={{textAlign: 'center', fontSize: '0.9em', paddingTop: '12px'}}>Already have account? <span onClick={showSignin} className='terms'>Login</span></p>
        </div>
      </div>
    );
  }
}

export default Signup;