import React from 'react';
import { IconContext } from 'react-icons';
import { FaPizzaSlice, FaFacebook, FaTwitter, FaInstagram, FaSpotify } from 'react-icons/fa';

import './Footer.scss';

const Footer = () => {
  return(
    <div className="footer">
      <div className='top'>
        <h4>Branches</h4>
        <p>bangalore</p>
        <p>chennai</p>
        <p>trichy</p>
        <p>coimbatore</p>
      </div>
      <div className='bottom'>
        <div className='titleLogo'>
          <IconContext.Provider value={{className: 'logoIcon'}}>
            <FaPizzaSlice /> 
          </IconContext.Provider>
          <h4>pizza hut</h4>
        </div>
        <div className='socialMedia'>
          <IconContext.Provider value={{className: 'mediaIcon'}}>
            <span><FaFacebook /></span>
            <span><FaTwitter /></span>
            <span><FaInstagram /></span>
            <span><FaSpotify /></span>
          </IconContext.Provider>
        </div>
      </div>
    </div>
  );
};

export default Footer;