import React from 'react';
import { withRouter } from 'react-router-dom';
import './Category.scss';

const pizzaUrl = 'https://www.snopes.com/tachyon/2018/01/pizza_fb.jpg?resize=865,452&quality=65';
const pastaUrl = 'https://wallpapercave.com/wp/wp4325872.jpg';

const Category = ({ history }) => {
  return(
    <div className='category'>
      <div className='categoryTitle'>
        <h3>collections</h3>
        <p>Explore all the dishes in pizza's and pasta's</p>
      </div>
      <div className='special'>
        <div className='pizza' onClick={() => history.push('/recipe/pizza')}>
          <img src={`${pizzaUrl}`} alt='category'/>
          <div className='details' />
          <div className='info'>
            <p className='infoTitle'>pizza</p>
            <p className='viewAll'>view all <span className='arrowIcon'>&#9658;</span></p>
          </div>
        </div>
        <div className='pasta' onClick={() => history.push('/recipe/pasta')}>
          <img src={`${pastaUrl}`} alt='category'/>
          <div className='details' />
          <div className='info'>
            <p className='infoTitle'>pasta</p>
            <p className='viewAll'>
              <span>view all</span>
              <span className='arrowIcon'>&#9658;</span>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withRouter(Category);