import React, { Component } from 'react';
import { NavLink, withRouter } from 'react-router-dom';
import { IconContext } from 'react-icons';
import { FaSearch } from 'react-icons/fa';

import './Header.scss';
import Button from '../Button/Button';
import Signup from '../Signup/Signup';
import Signin from '../Signin/Signin';
import { auth } from '../../firebase/firebase';
import { UserContext } from '../../context/UserContext';

class Header extends Component {
  state = {
    showSignup: false,
    showSignin: false
  }

  static contextType = UserContext;

  toggleSignup = () => {
    this.setState(state => ({
      showSignup: !state.showSignup,
      showSignin: false
    }));
  }

  toggleSignin = () => {
    this.setState(state => ({
      showSignin: !state.showSignin,
      showSignup: false
    }));
  }

  render() {
    const { history } = this.props;
    const { showSignup, showSignin } = this.state;
    const { currentUser } = this.context;

    return(
      <header>
        <div className='logoTitle'>
          <h3>pizza hut</h3>
        </div>
        <nav>
          <div className='navLinks'>
            <NavLink to='/' exact title='go back to home page'>Home</NavLink>
            <NavLink to='/cart' className='cart' title='check your wishlist'>My Wishlist</NavLink>
            <NavLink to='/likes' title='check your favourites'>Favourites</NavLink>
            {
              currentUser
              ? <Button title='logout' click={() => auth.signOut()}>logout</Button>
              : <>
                <Button title='login to order' click={this.toggleSignin}>login</Button>
                <Button primary title='instant order' click={this.toggleSignup}>join now</Button>
              </>
            }
          </div>
            <IconContext.Provider value={{ className: 'searchIcon' }}>
              <FaSearch onClick={() => history.push("/search")}/>
            </IconContext.Provider>
            {showSignup && <Signup show={showSignup} showSignin={this.toggleSignin} close={this.toggleSignup}/>}
            {showSignin && <Signin show={showSignin} showSignup={this.toggleSignup} close={this.toggleSignin}/>}
        </nav>
      </header>
    );
  }
};

export default withRouter(Header);