import { createSelector } from 'reselect';
import { recipePrice } from '../../utility/recipe';

const cart = state => state.cart;

export const selectRecipes = createSelector(
  [cart],
  cart => cart.recipes
);

export const totalPrice = createSelector(
  [selectRecipes],
  recipes => recipes.reduce((acc, cur) => acc + (recipePrice(cur.recipe_id) * cur.quantity), 0)
);