const syncLocalStorage = list => {
  window.localStorage.setItem('recipes', JSON.stringify(list));
};

export const additem = (currentItems, itemToAdd) => {
  const hasItem = currentItems.find(item => item.recipe_id === itemToAdd.recipe_id);
  
  let recipes = [];
  if (!hasItem) {
    recipes = currentItems.concat({...itemToAdd, quantity: 1});
    syncLocalStorage(recipes);
    return recipes;
  }

  recipes = currentItems;
  syncLocalStorage(recipes);
  return recipes;
};

export const removeItem = (currentItems, itemToRemove) => {
  const hasItem = currentItems.find(item => item.recipe_id === itemToRemove.recipe_id);
  
  let recipes = [];
  if (!hasItem) {
    recipes = currentItems;
    syncLocalStorage(recipes);
    return recipes;
  }

  recipes = currentItems.filter(item => item.recipe_id !== itemToRemove.recipe_id);
  syncLocalStorage(recipes);
  return recipes;
};

export const hasRecipe = (cartItems, recipe) => {
  return cartItems.findIndex(item => item.recipe_id === recipe.recipe_id) !== -1;
};

export const incrementQuantity = (cartItems, itemToAdd) => {
  const hasItem = cartItems.find(item => item.recipe_id === itemToAdd.recipe_id);

  let list = [];
  if (hasItem) {
    list = cartItems.map(item => 
      item.recipe_id === itemToAdd.recipe_id
      ? { ...item, quantity: item.quantity + 1}
      : item);
    syncLocalStorage(list);
    return list;
  }

  list = cartItems.concat({...itemToAdd, quantity: 1});
  syncLocalStorage(list);
  return list;
}

export const decrementQuantity = (cartItems, itemToReduce) => {
  const hasItem = cartItems.find(item => item.recipe_id === itemToReduce.recipe_id);

  let list = [];
  if (hasItem.quantity > 1) {
    list = cartItems.map(item => 
      item.recipe_id === itemToReduce.recipe_id
      ? { ...item, quantity: item.quantity - 1}
      : item);
    syncLocalStorage(list);
    return list;
  }

  list = cartItems;
  syncLocalStorage(list);
  return list;
}