import React, { Component } from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';

import './Wishlist.scss';
import CartItem from '../../components/CartItem/CartItem';
import Button from '../../components/Button/Button';
import Checkout from '../../components/Checkout/Checkout';
import Popup from '../../components/Popup/Popup';

import { selectRecipes, totalPrice } from '../../Redux/cart/cartSelector';
import { removeRecipe, increment, decrement } from '../../Redux/cart/cartAction';
import { UserContext } from '../../context/UserContext';
import Backdrop from '../../components/Backdrop/Backdrop';

class Wishlist extends Component {
  state = {
    showCheckout: false
  }

  static contextType = UserContext;

  toggleCheckout = () => {
    this.setState(state => ({
      showCheckout: !state.showCheckout
    }));
  }

  render() {
    const { cartItems, price, remove, incQuantity, decQuantity } = this.props;
    const { showCheckout } = this.state;
    const { currentUser } = this.context;

    return(
      <div>
        {showCheckout && <Backdrop close={this.toggleCheckout}/>}
        {showCheckout && 
          <Popup show={showCheckout}>
            <Checkout show={showCheckout} close={this.toggleCheckout}/>
          </Popup>}
        <div className='wishList'>
          <div className='titleContainer'>
            <h3>wishlist</h3>
            <p>recipes added to your wishlist</p>
          </div>
          {
            cartItems.length > 0 ? cartItems.map(recipe => (
              <CartItem 
                key={recipe.recipe_id} 
                wishlist recipe={recipe} 
                click={() => remove(recipe)} 
                increase={() => incQuantity(recipe)}
                decrease={() => decQuantity(recipe)}/>
            )) : <p className='emptyMsg'>your wishlist is empty</p>
          }
          <div className='checkout'>
            <span className='totalPrice'>total price: $ {price}</span>
            {currentUser 
              ? <Button primary click={this.toggleCheckout}>checkout</Button> 
              : <Button>signin to order</Button>}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  cartItems: selectRecipes,
  price: totalPrice
});

const mapDispatchToProps = dispatch => ({
  remove: recipe => dispatch( removeRecipe(recipe) ),
  incQuantity: recipe => dispatch( increment(recipe) ),
  decQuantity: recipe => dispatch( decrement(recipe) )
});

export default connect(mapStateToProps, mapDispatchToProps)(Wishlist);