import React, { forwardRef } from 'react';

import './Input.scss';

const Input = (props, ref) => {
  const { label, name, change, ...rest } = props;

  return(
    <div className='input'>
      <label>{label}</label>
      <input id={name} name={name} onChange={change} ref={ref} {...rest}/>
    </div>
  );
};

export default forwardRef(Input);